@import "https://unpkg.com/reset-css/reset.css";
@import "https://fonts.googleapis.com/css2?family=League+Gothic&display=swap";
:root {
  --dark: #22252a;
  --default: #999;
  --light: #ccc;
  --white: #e6e6e6;
  --accent: #b9c949;
}

body {
  background-color: var(--dark);
  background-image: url("background_toplight.969ab129.png"), linear-gradient(#0000001f, #0000), url("background_tile.84975cd3.png");
  background-position: top, 0 0, 0 0;
  background-repeat: no-repeat, repeat-x, repeat;
  background-size: 1200px 375px, 100px 460px, 200px 200px;
  grid-template-rows: 100vh;
  grid-template-columns: minmax(auto, 1200px);
  justify-content: center;
  display: grid;
}

.page {
  grid-template: "header" 1fr
                 "main" 1fr
                 "footer" 1fr
                 / 1fr;
  display: grid;
}

.logo {
  grid-area: header;
  align-self: center;
  margin: 5% 15%;
}

h1 {
  color: var(--accent);
  letter-spacing: 1px;
  font-family: League Gothic, sans-serif;
}

